	module.exports = {"data":{"post":{"timeToRead":3,"canonicalURL":"https://blog.iansinnott.com/teaching-my-brother-to-code/","html":"<p>I just returned to the US after a few months in Asia, and my goal for the month of December is to teach my brother how to code. One month, that's how much time I have to get him up to speed. Sound crazy? It might be, but that depends on the concrete goal. \"Learn to code\" is far too abstract.</p>\n<h2 id=\"the-goal\"><a href=\"#the-goal\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>The Goal</h2>\n<p>In one month's time I intend to get my brother up to speed with front-end development. But \"up to speed\" does not mean he will be writing the next front-end framework in just one month. My goal is more along the lines of quickly smashing through the usual barriers people face when they want to learn code. These usually include things like:</p>\n<ul>\n<li>All sorts of mental barriers (\"It's too hard.\", \"I'm starting too late?\", \"Where do I start?\")</li>\n<li>Frustration</li>\n<li>Time constraints. (If you have a full-time job it might be hard to find the time to code.)</li>\n</ul>\n<p>When I learned to code those first two points were the biggest barriers to progress. Not knowing where to start is a big hurdle to overcome if you are brand new to coding and don't know anyone in the field. Luckily I do this for a living so I can help my bro get passed that pretty quickly.</p>\n<p>Over the next month, I see frustration as the largest obstacle to progress, especially since we're focusing on the front-end.</p>\n<p>Moreover, I hope to give him enough knowledge that he can continue comfortably on his own. <em>That</em> is the ultimate goal.</p>\n<h2 id=\"curriculum\"><a href=\"#curriculum\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Curriculum</h2>\n<p>Since we're focusing on the front-end, I'll be teaching the classic front-end technologies:</p>\n<ul>\n<li>HTML</li>\n<li>CSS</li>\n<li>JavaScript</li>\n</ul>\n<p>Each have their own challenges.</p>\n<h4 id=\"html\"><a href=\"#html\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>HTML</h4>\n<p>HTML is busywork. It's not particularly difficult to understand or implement, the number of tags and attributes are all quite limited and there's no logic to consider. That being said, it's just not interesting. In fact, writing HTML is intensely boring, especially once you know what you're doing. (That's why I moved to Jade ðŸ˜„.)</p>\n<h4 id=\"css\"><a href=\"#css\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>CSS</h4>\n<p>CSS is a terrible language. It was designed for a very different web than we design for today. Styling text is usually simple enough, but when you want to get into any sort of layout styling things can get very frustrating very fast. Want to put something on the right? Just use <code>float: right;</code> and everything will be fine... until your containing div collapses and you have to figure out what \"clearfix\" means and how to implement it. Or you could just use <code>overflow: hidden;</code>, but that is unintuitive <em>at best</em> and at worst can cause issues with absolutely positioned elements and shadows.</p>\n<p>There are a million other ways CSS can frustrate people, since so many of its properties simply don't work the way you expect, or you need to ensure certain display properties to make them work as advertised.</p>\n<p>The other issue with CSS in my mind is that like HTML, it's boring. There's more opportunity in CSS to write good code, but it still lacks logic and lacks features that seem wildly obvious (Variables anyone?).</p>\n<p>For a <strong>partial solution</strong>, learn CSS well then never touch it again and only use <a href=\"http://learnboost.github.io/stylus/\">Stylus</a>.</p>\n<h4 id=\"javascript\"><a href=\"#javascript\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>JavaScript</h4>\n<p>Finally, the actual programming language of the front-end. I haven't yet started teaching this to my brother, but I'm looking forward to it. Real programming is a thrill once you gain even a tiny amount of knowledge. Being able to create things from scratch that are actually useful is liberating; at least it was for me. By the end of this makeshift bootcamp I hope my brother feels the same way.</p>\n<h2 id=\"stay-tuned\"><a href=\"#stay-tuned\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Stay tuned</h2>\n<p>I'll be posting updates as time goes on.</p>","frontmatter":{"created":"2014-12-07T08:00:00.000Z","title":"Teaching My Brother To Code","disqusId":null}}},"pathContext":{"id":"/Users/ian/dev/sites/blog.iansinnott.com/content/_posts/2014-12-07-teaching-my-brother-to-code.md absPath of file >>> MarkdownRemark","next":{"frontmatter":{"title":"A Site a Day","created":"2014-12-08T08:00:00.000Z"},"slug":"a-site-a-day"},"prev":{"frontmatter":{"title":"Running a Meteor App on Dokku","created":"2014-11-22T08:00:00.000Z"},"slug":"running-a-meteor-app-on-dokku"}}}

